<template>
  <el-col :span="6">
    <i class="iconfont icon-Palettetiaoseban" @click="bjClick"></i>
    <div class=" bg-purple-light">
      <i class="iconfont icon-sousuo"></i>
      <input type="search" @focus="focusFn" @blur="blurFn" :class="{ focus: isFocus }" id="search" />
    </div>
    <div id="bj" :style="{ display: isTsb }">
      <p>调色板</p>
      <div id="tsb">
        <div @click="tsbClick1" :class="{ bjc: isColor == 1 }">黑夜</div>
        <div @click="tsbClick2" :class="{ bjc: isColor == 2 }">日间</div>
        <div @click="tsbClick3" :class="{ bjc: isColor == 3 }">保留</div>
      </div>
    </div>
  </el-col>
</template>

<script>
export default {
  methods: {
    focusFn() {
      this.isFocus = true;
    },
    blurFn() {
      this.isFocus = false;
    },
    bjClick() {
      if (this.isTsb == "block") {
        this.isTsb = "none";
      } else {
        this.isTsb = "block";
      }
    },
    tsbClick1() {
      this.isColor = 1;
      this.$store.state.bodyBg = true;
    },
    tsbClick2() {
      this.isColor = 2;
      this.$store.state.bodyBg = false;
    },
    tsbClick3() {
      this.isColor = 3;
    },
  },
  data() {
    return {
      isFocus: false,
      isTsb: "none",
      isColor: 2,
    };
  },
};
</script>

<style scoped>
  .el-col {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .icon-Palettetiaoseban {
    flex: 1;
    color: rgb(62, 175, 124);
  }
  .bg-purple-light {
    position: relative;
    flex: 9;
  }
  .icon-sousuo {
    position: absolute;
    left: 6px;
    line-height: 32px;
  }
  #bj {
    position: absolute;
    left: -60px;
    top: 50px;
    width: 153px;
    height: 82px;
    padding: 15px;
    background-color: white;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    background-color: transparent;
  }
  p {
    height: 16px;
    line-height: 16px;
  }
  #tsb {
    width: 100%;
    height: 25px;
    border: 1px solid gray;
    margin-top: 8px;
  }
  #tsb > div {
    width: 33.3333%;
    height: 25px;
    font-size: 16px;
    line-height: 25px;
    float: left;
    cursor: pointer;
  }
  #tsb > div:nth-child(2) {
    border-left: 1px solid gray;
    border-right: 1px solid gray;
  }
  .bjc {
    background-color: rgb(62, 175, 124);
  }
  #search {
    height: 2rem;
    outline: 0;
    padding-left: 1.6rem;
    border: 1px solid gray;
    background-color: transparent;
  }
  .focus {
    border-color: rgb(62, 175, 124) !important;
  }
</style>