<template>
  <div id="app" @click.stop.prevent="clickFn" :class="{ black: this.$store.state.bodyBg }">
    <main-nav></main-nav>
    <router-view></router-view>
    <transition name="slide-fade">
      <div v-if="show" class="hxjzg" :style="{ top: isTop, left: isLeft, color: isColor[index] }">
        {{ hxjzg[index] }}
      </div>
    </transition>
  </div>
</template>

<script>
import mainNav from "./components/mainHead.vue";
export default {
  name: "app",
  components: {
    mainNav,
  },
  methods: {
    clickFn(e) {
      if (this.fd) return;
      this.index++;
      this.show = true;
      this.fd = true;
      this.isTop = e.pageY + "px";
      this.isLeft = e.pageX - 20 + "px";
      this.index >= 12 ? (this.index = 0) : this.index;
      setTimeout(() => {
        this.show = false;
      }, 0);
      setTimeout(() => {
        this.fd = false;
      }, 300);
    },
  },
  data() {
    return {
      isTop: 0,
      isLeft: 0,
      show: false,
      fd: false,
      isColor: [
        "red",
        "royalblue",
        "orange",
        "green",
        "blue",
        "orangered",
        "red",
        "red",
        "orange",
        "crimson",
        "brown",
        "mediumblue",
      ],
      hxjzg: [
        "♥富强♥",
        "♥民主♥",
        "♥文明♥",
        "♥和谐♥",
        "♥自由♥",
        "♥平等♥",
        "♥公正♥",
        "♥法治♥",
        "♥爱国♥",
        "♥敬业♥",
        "♥诚信♥",
        "♥友善♥",
      ],
      index: 0,
    };
  },
};
</script>

<style>
  .black {
    background-color: rgb(24, 24, 24);
    color: #fff;
  }
  #app {
    position: relative;
    height: 100%;
  }
  .hxjzg {
    position: absolute;
    z-index: 999;
    font-size: 12px;
    cursor: default;
  }
  .slide-fade-leave-active {
    transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);
  }
  .slide-fade-leave-to {
    transform: translateY(-30px);
    opacity: 0;
  }
  @import url("./assets/css/base.css");
  @import url("./assets/icon/iconfont.css");
</style>
